import {
    db
} from "@/firebase/init";

export default {
    namespaced: true,
    state() {
        return {
            payment: {},
            payments: []
        }
    },
    actions: {
        //
        getPayments({
            commit
        }) {
            commit('loading', true, {
                root: true
            })
            commit('setPayments', [])
            return db.collection('payments')
                .orderBy('createdAt', 'desc')
                .get()
                .then(snapshots => {
                    const payments = snapshots.docs.map(snapshot => ({
                        id: snapshot.id,
                        ...snapshot.data()
                    }))
                    commit('setPayments', payments)
                    commit('loading', false, {
                        root: true
                    })
                    return payments
                })

        },

        //
        getPaymentById({
            commit
        }, paymentId) {
            commit('loading', true, {
                root: true
            })
            db.collection('payments')
                .doc(paymentId)
                .get()
                .then(snapshot => {
                    const payment = snapshot.data()
                    payment.id = snapshot.id
                    commit('setPayment', payment)
                    commit('loading', false, {
                        root: true
                    })
                })
        },

        //
        createPayment({
            commit
        }, payment) {
            payment.status = ""
            payment.createdAt = Date.now()
            db.collection('payments')
                .add(payment)
                .then(docRef => {
                    payment.id = docRef.id;
                    //id
                    commit('setPayment', payment)
                })
        },

        //
        finishPayment({
            commit
        }, id) {
            db.collection('payments')
                .doc(id)
                .update({
                    status: '',
                    finishedAt: Date.now()
                })
                .then(() => commit('changePaymentsStatus', {
                    id: id,
                    status: '',
                    finishedAt: Date.now()
                }))
        }

    },
    mutations: {
        //
        setPayments(state, payments) {
            state.payments = payments
        },

        //
        setPayment(state, payment) {
            state.payment = payment
        },

        //
        changePaymentsStatus(state, {
            status,
            finishedAt
        }) {
            state.payment.status = status
            state.payment.finishedAt = finishedAt
        },

        //
        clearPayment(state) {
            console.log('yo');
            
            state.payment = {}
        }
    }
}