import {
    db
} from "@/firebase/init";
import Vue from 'vue'

export default {
    namespaced: true,
    state() {
        return {
            comments: [],
        }
    },
    actions: {

        //
        getComments({
            commit
        }, id) {
            // commit('setComments', [])
            return db.collection('comments')
                .where('toId', '==', id)
                .get()
                .then(async snapshots => {
                    //
                    const comments = await snapshots.docs.map(snapshot => {
                        let comment = {
                            id: snapshot.id,
                            ...snapshot.data()
                        }

                        //
                        db.collection('users')
                            .doc(comment.user)
                            .get()
                            .then(async snapshot => {
                                comment.user = await snapshot.data()
                            })
                        return comment
                    });
                    commit('setComments', comments)
                })
        },



        //
        createComment({
            commit
        }, {
            comment,
            user
        }) {
            comment.timestamp = Date.now()
            db.collection('comments')
                .add(comment)
                .then(docRef => {
                        //(cloud function)
                        comment.user = user.profile
                        comment.id = docRef.id
                        commit('addComment', comment)
                    }
                )
        },

        //
        updateComment({
                commit
            },
            comment,
        ) {
            comment.timestamp = Date.now()
            db.collection('comments')
                .doc(comment.id)
                .update({
                    comment: comment.comment,
                    rating: comment.rating,
                    timestamp: comment.timestamp
                })
                .then(() => {
                    commit('changeComment', comment)
                })
        }
    },
    mutations: {
        //
        setComments(state, comments) {
            state.comments = comments
        },

        //commentlist
        addComment(state, comment) {
            state.comments.push(comment)
        },

        // comment
        changeComment(state, comment) {
            const index = state.comments.findIndex(item => item.id === comment.id)
            // state.comments[index] = comment
            // or
            Vue.set(state.comments[index], 'comment', comment.comment)
            Vue.set(state.comments[index], 'rating', comment.rating)
            Vue.set(state.comments[index], 'timestamp', comment.timestamp)
        }
    }

}